.main {
  position: relative;
  padding: 0 2 rem;
}
.navbar {
  width: 100%;
  height: 60px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.link {
  display: flex;
  gap: 2rem;
}
.toggleBtn {
  color: #fff;
  cursor: pointer;
  display: none;
}
.toggleBtn img {
  width: 1.5rem;
}
.actionBtn {
  background-color: orange;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  outline: none;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: bold;
  cursor: pointer;
  transition: scale 0.2 ease;
}
.actionBtn:hover {
  scale: 1.05;
  color: #fff;
}
.actionBtn:active {
  scale: 0.95;
}
/* Dropdown Menu */
.dropDownMenu {
  position: absolute;
  right: 2rem;
  top: 60px;
  width: 300px;
  background: rgb(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  border-radius: 10px;
  overflow: hidden;
  transition: height .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.dropDownMenu li {
  padding: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.dropDownMenu .actionBtn {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* Responsive Design */
@media (min-width: 992px){
    .dropDownMenu{
        display: none;
    }
}
@media (max-width: 992px) {
  .link,
  .navbar .actionBtn {
    display: none;
  }
  .toggleBtn {
    display: block;
  }
  .dropDownMenu{
    display: block;
  }
}
@media (max-width: 576px) {
    .dropDownMenu{
        left: 2rem;
        width: unset;
    }
}